}

.tagStyleMango {
    font-color: #A93E00;
    background-color: #FFF4B8; 
}

.tagStyleTeal {
    font-color: #046C7A;
    background-color: #C2FCEE; 
}

.tagStyleGrey {
    font-color: #354B5F;
    background-color: #EAECEE; 
